
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

#FileList{
    position: fixed;
    top: 80px;
    left: 200px;
    width: 300px;
    height: 500px;
    padding: 10px;
    overflow-y: auto;
    overflow-x: auto;
    background: rgba(230, 230, 230, 1);
    /* text-align: center; */
    border-left: rgb(150,150,150) solid 1px;
}
#menu {
    position: fixed;
    top: 80px;
    width: 200px;
    height: 500px;
    padding: 10px;
    /* padding-bottom: 300px; */
    background: rgba(230, 230, 230, 1);
    text-align: center;
    overflow-y: auto;
}
.menu-item {
    margin-top: 10px;
    padding: 4px;
    border-radius: 3px;
    /* transition: all 0.1s; */
}
.menu-item:hover {
    cursor: pointer;
    background: rgb(200, 200, 200);
}
hr {
    margin: 3px;
}
#nav {
    width: 100%;
    height: 80px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    padding-left: 30px;
    background: rgb(235, 235, 235);
}
#nav-menu-div {
    padding: 5px;
    height: 50px;
    width: 50px;
}
#menu-img {
    width: 40px;
    height: 40px;
    margin-top: 10px;
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
}
#nav-menu-div:hover {
    cursor: pointer;
}
#nav-menu-div:hover img {
    margin-top: 7px;
    margin-left: -3px;
    width: 46px;
    height: 46px;
}
.nav-img {
    width: 70px;
    height: 45px;
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
}
.nav-div {
    height: 55px;
    width: 95px;
    padding: 5px;
    padding-top: 0;
    margin-left: 20px;
    text-align: center;
}
.nav-div:hover {
    cursor: pointer;
}
.nav-div:hover img {
    width: 76px;
    height: 53px;
}
.addCol_Row:hover {
    cursor: pointer;
    background: rgb(200, 200, 200);
}
#index-table-div {
    width: 95%;
    height: 700px;
    overflow-y: auto;
    overflow-x: auto;
    margin: 30px;
    margin-top: 5px;
    margin-bottom: 0;
    padding-top: 5px;
    padding-right: 20px;
    padding-bottom: 5px;
    background: rgb(235, 235, 235);
}
.table-head {
    border: none;
    outline: none;
}
.table-head-input {
    border: rgb(150, 150, 150) solid 1px;
    background: rgb(220, 220, 220);
}
.my-td-input {
    width: 60px;
    height: 30px;
    text-align: center;
}
.my-cowNum:hover {
    cursor: pointer;
    background: rgb(200, 200, 200);
}
.my-rowNum:hover {
    cursor: pointer;
    background: rgb(200, 200, 200);
}








































































